import React, { Component } from 'react';
import { NavLink, Link } from 'react-router-dom'
import { Collapse } from 'react-bootstrap';
import { useLocation } from '../../Services/Utils/utils';

export default class AsideMenu extends Component {
    constructor() {
        super();
        this.state = {
            support: false
        };
    }
    submenuDropdown = (val) => {
        if (val === 'collections') {
            this.setState({
                support: false
            })
        } else {
            this.setState({
                support: !this.state.support
            })
        }
    }
    closeAsideToggle = (val) => {
        if (val === 'main') {
            this.setState({
                support: false
            })
        } else if (val === 'child-menu') {
            this.setState({
                support: false
            })
        }
        var rootEle = document.getElementById('ugc-suit-main');
        if (rootEle) {
            rootEle.classList.remove('aside-on');
        }
    }

    render() {
        const { support } = this.state;
        return <div className="sidebar-menu">
            <ul className="sidebar-menu-list list-unstyled mb-0 pe-3 pe-xxl-4 ps-2">
                <li className="sidebar-menu-items">
                    <Link to="/home" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 22 19.648">
                                <path fill="none" d="M0 0H24V24H0z" />
                                <g><path id="Path_2323" data-name="Path 2323" d="M6,19H18V9.157L12,3.7,6,9.157Zm13,2H5a1,1,0,0,1-1-1V11H1L11.327,1.612a1,1,0,0,1,1.346,0L23,11H20v9A1,1,0,0,1,19,21ZM7.5,13h2a2.5,2.5,0,1,0,5,0h2a4.5,4.5,0,0,1-9,0Z" transform="translate(-1 -1.352)" fill="#313a53"></path></g>
                            </svg>
                        </span>
                        <span className="menu-text">Home</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/assets" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" exact={useLocation} activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <path fill="none" d="M0 0H24V24H0z" />
                                <g><path d="M4.828 21l-.02.02-.021-.02H2.992A.993.993 0 0 1 2 20.007V3.993A1 1 0 0 1 2.992 3h18.016c.548 0 .992.445.992.993v16.014a1 1 0 0 1-.992.993H4.828zM20 15V5H4v14L14 9l6 6zm0 2.828l-6-6L6.828 19H20v-1.172zM8 11a2 2 0 1 1 0-4 2 2 0 0 1 0 4z" fill="#313a53" /></g>
                            </svg>
                        </span>
                        <span className="menu-text">My Assets</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/manage-connection" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <path fill="none" d="M0 0H24V24H0z" />
                                <g><path id="Path_2321" data-name="Path 2321" d="M21,14v5a2,2,0,0,1-2,2H5a2,2,0,0,1-2-2V5A2,2,0,0,1,5,3h5V5H5V19H19V14Z" fill="#313a53" />
                                    <path id="Path_2322" data-name="Path 2322" d="M21,7H17V3H15V7H11V9h4v4h2V9h4" fill="#313a53" /></g>
                            </svg>
                        </span>
                        <span className="menu-text">Connections</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/template" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <path fill="none" d="M0 0h24v24H0z" />
                                <g>
                                    <path d="M20 3a1 1 0 0 1 1 1v16a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h16zm-9 10H5v6h6v-6zm2 6h6V5h-6v14zM11 5H5v6h6V5z" fill="#313a53" />
                                </g>
                            </svg>
                        </span>
                        <span className="menu-text">Templates</span>
                    </Link>
                </li>
                <li className="sidebar-menu-items">
                    <Link to="/rule" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                <g>
                                    <path id="Path_11314" data-name="Path 11314" d="M0,0H24V24H0Z" fill="none" />
                                    <path id="Path_11315" data-name="Path 11315" d="M20,2a1,1,0,0,1,1,1V6.757l-2,2V4H5V20H19V17.242l2-2V21a1,1,0,0,1-1,1H4a1,1,0,0,1-1-1V3A1,1,0,0,1,4,2Zm1.778,6.808,1.414,1.414L15.414,18,14,18l0-1.412,7.778-7.778ZM13,12v2H8V12Zm3-4v2H8V8Z" fill="#313a53" />
                                </g>
                            </svg>
                        </span>
                        <span className="menu-text">Rules</span>
                    </Link >
                </li>
            </ul>
            <hr className="my-4" />
            <ul className="sidebar-menu-list list-unstyled mb-0 pe-3 pe-xxl-4 ps-2">
                <li className="sidebar-menu-items">
                    <Link to="/analytics" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <g>
                                    <path fill="none" d="M0 0H24V24H0z" />
                                    <path d="M5 3v16h16v2H3V3h2zm15.293 3.293l1.414 1.414L16 13.414l-3-2.999-4.293 4.292-1.414-1.414L13 7.586l3 2.999 4.293-4.292z" fill="#313a53" />
                                </g>
                            </svg>
                        </span>
                        <span className="menu-text">Analytics</span>
                    </Link >
                </li>
                <li className={`sidebar-menu-items ${support ? 'active' : ''}`}>
                    <span className="menu-toggle nav-menu-item d-flex align-items-center fw-bold text-decoration-none justify-content-between" onClick={() => this.submenuDropdown('support')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <path fill="none" d="M0 0H24V24H0z" />
                                <g><path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm0-2a8 8 0 1 0 0-16 8 8 0 0 0 0 16zm-1-5h2v2h-2v-2zm2-1.645V14h-2v-1.5a1 1 0 0 1 1-1 1.5 1.5 0 1 0-1.471-1.794l-1.962-.393A3.501 3.501 0 1 1 13 13.355z" fill="currentColor" /></g>
                            </svg>
                        </span>
                        <span className="menu-text flex-grow-1">Support</span>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" height="12" width="12" className="menu-arrow fill-primary">
                            <path fill="none" d="M0 0H24V24H0z" />
                            <g><path d="M278.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-160 160c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L210.7 256 73.4 118.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l160 160z" fill="currentColor" /></g>
                        </svg>
                    </span>
                    <div className="menu-submenu">
                        <Collapse in={support}>
                            <ul className="submenu-nav list-unstyled mb-0">
                                <li className="submenu-nav-item mt-2">
                                    <Link className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active"
                                        //onClick={() => this.closeAsideToggle('child-menu')} 
                                        onClick={window.Intercom ? (e) => { window.Intercom('show') } : () => { }}>
                                        <span className="menu-icon">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M7.291 20.824L2 22l1.176-5.291A9.956 9.956 0 0 1 2 12C2 6.477 6.477 2 12 2s10 4.477 10 10-4.477 10-10 10a9.956 9.956 0 0 1-4.709-1.176zm.29-2.113l.653.35A7.955 7.955 0 0 0 12 20a8 8 0 1 0-8-8c0 1.334.325 2.618.94 3.766l.349.653-.655 2.947 2.947-.655z" fill="currentColor" />
                                            </svg>
                                        </span>
                                        <span className="menu-text">Chat</span>
                                    </Link >
                                </li>
                                <li className="submenu-nav-item">
                                    <a href="tel:+1628900-3005" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M9.366 10.682a10.556 10.556 0 0 0 3.952 3.952l.884-1.238a1 1 0 0 1 1.294-.296 11.422 11.422 0 0 0 4.583 1.364 1 1 0 0 1 .921.997v4.462a1 1 0 0 1-.898.995c-.53.055-1.064.082-1.602.082C9.94 21 3 14.06 3 5.5c0-.538.027-1.072.082-1.602A1 1 0 0 1 4.077 3h4.462a1 1 0 0 1 .997.921A11.422 11.422 0 0 0 10.9 8.504a1 1 0 0 1-.296 1.294l-1.238.884zm-2.522-.657l1.9-1.357A13.41 13.41 0 0 1 7.647 5H5.01c-.006.166-.009.333-.009.5C5 12.956 11.044 19 18.5 19c.167 0 .334-.003.5-.01v-2.637a13.41 13.41 0 0 1-3.668-1.097l-1.357 1.9a12.442 12.442 0 0 1-1.588-.75l-.058-.033a12.556 12.556 0 0 1-4.702-4.702l-.033-.058a12.442 12.442 0 0 1-.75-1.588z" fill="currentColor" />
                                            </svg>
                                        </span>
                                        <span className="menu-text">Call</span>
                                    </a>
                                </li>
                                <li className="submenu-nav-item">
                                    <a href="mailto:support@taggbox.com" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M3 3h18a1 1 0 0 1 1 1v16a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1zm17 4.238l-7.928 7.1L4 7.216V19h16V7.238zM4.511 5l7.55 6.662L19.502 5H4.511z" fill="currentColor" />
                                            </svg>
                                        </span>
                                        <span className="menu-text">Email</span>
                                    </a>
                                </li>
                                <li className="submenu-nav-item">
                                    <Link to="/support" className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('child-menu')}>
                                        <span className="menu-icon">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M21 4H7a2 2 0 1 0 0 4h14v13a1 1 0 0 1-1 1H7a4 4 0 0 1-4-4V6a4 4 0 0 1 4-4h13a1 1 0 0 1 1 1v1zM5 18a2 2 0 0 0 2 2h12V10H7a3.982 3.982 0 0 1-2-.535V18zM20 7H7a1 1 0 1 1 0-2h13v2z" fill="currentColor" />
                                            </svg>
                                        </span>
                                        <span className="menu-text">Knowledge</span>
                                    </Link>
                                </li>
                            </ul>
                        </Collapse>
                    </div>
                </li>
                <li className="sidebar-menu-items">
                    <Link to={'/trash'} className="nav-menu-item d-flex align-items-center fw-bold text-decoration-none" activeclassname="active" onClick={() => this.closeAsideToggle('main')}>
                        <span className="menu-icon">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                <path fill="none" d="M0 0H24V24H0z" />
                                <g><path d="M17 6h5v2h-2v13a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V8H2V6h5V3a1 1 0 0 1 1-1h8a1 1 0 0 1 1 1v3zm1 2H6v12h12V8zm-9 3h2v6H9v-6zm4 0h2v6h-2v-6zM9 4v2h6V4H9z" fill="currentColor" /></g>
                            </svg>
                        </span>
                        <span className="menu-text">Trash</span>
                    </Link>
                </li>
            </ul>
        </div>
    }
}
